import { render } from '@testing-library/react';
import { useState, useEffect, Component } from 'react';
import { API_URL } from '../config';
import '../models/card-selector';
import '../style.css';
import axios from 'axios';
import ExamItem from './ExamItem';

export default class ExamSection extends Component {
    constructor(props) {
        super(props);
   
        this.state = {
            items: [],
            isLoaded: false,
            error: null
        };
    }

    componentDidMount() {
        fetch(API_URL + '/exams')
            .then(result => result.json())
            .then(data => {
                this.state = {
                    isLoaded: true,
                    items: data.items,
                    error: null
                };
                console.log(this.state.isLoaded);
            }, error => {
                this.state = {
                    isLoaded: true,
                    error
                };
            });
    }

    render() {
        let data = axios(API_URL + '/exams');
        console.log(data);

        return (
            <div className="row justify-content-center">
                <div className="col-md-9">
                    <div className="card text-center justify-content-center shaodw-lg  card-1 border-0 bg-white px-sm-2">
                        <div className="card-body show  ">
                            <div className="row">
                                <div className="col">
                                    <h5><b>ЗНО</b></h5>
                                    <p> Оберіть ваші ЗНО <span className=" ml-1 cursor-pointer"><a href='https://testportal.gov.ua/'>Детальніше</a></span> </p>
                                </div>
                            </div>
                            <div className="radio-group row justify-content-between px-3 text-center a">
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}